import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"

export default function About({ data }) {
	return (
		<Layout>
			<h2>About {data.site.siteMetadata.title}</h2>
			<p>
				Hello, my name is Habdul Hazeez welcome to my blog. I teach and write
				code with interest in Web Development, Computer Security, and Artificial
				Intelligence.
			</p>

			<p>I use the following tools:</p>

			<ul>
				<li>PHP: Hypertext Pre-processor</li>
				<li>CSS</li>
				<li>JavaScript</li>
				<li>MySQL database.</li>
			</ul>

			<p>
				I can use the tools that is suitable for the project (
				<em>programming language</em>, <em>library</em>, <em>framework</em>{" "}
				e.t.c) even If I have to learn it from scratch, don't worry{" "}
				<strong>I am a pretty fast learner.</strong>
			</p>
			<p>
				Prior to creating this blog, I publish on the{" "}
				<a href="https://dev.to/ziizium">ThePracticalDEV</a> (
				<em>I still do</em>) but now I have decided to have a place I can call{" "}
				<em>my own</em> but I am still going to be publishing on
				ThePracticalDEV.
			</p>

			<p>
				In its <strong>current state</strong> this blog is still{" "}
				<strong>basic and a work in progress</strong> therefore, you should
				expect to see changes frequently.
			</p>

			<p>
				The blog is built with GatsbyJS after following the{" "}
				<em>official tutorial</em> which means{" "}
				<strong>I am still learning</strong> GatsbyJS and its ecosystem. I'll
				constantly update this site as I proceed in my learning.
			</p>

			<p>
				You should expect a <em>change log</em> link somewhere in the{" "}
				<em>header</em> to track my progress and how this blog will evolve.
			</p>

			<p>Thank you for the visit!</p>
		</Layout>
	)
}

export const query = graphql`
	query {
		site {
			siteMetadata {
				title
			}
		}
	}
`
